@import "../../styles/partials/global";

.landing-container {
  background-image: linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)),
    url(../../assets/hero.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  min-height: 100vh;
  display: flex;
  justify-content: center;
  &__title-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
  }
  &__title {
    display: flex;
    justify-content: center;
    width: 84.25vw;
    letter-spacing: 7px;
    // width: 66.25vw;
    margin: 0;
    padding-top: 9.375rem;
    color: $white;
    opacity: 90%;
    background: -webkit-linear-gradient(45deg, #eee, #333);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    font-size: 20.3125vw;
    text-shadow: 0 0px 12px rgba(255, 255, 255, 1);

    @include tablet {
      width: 66.255vw;
      letter-spacing: 11px;
      // width: 53.255vw;
      font-size: 16.3125vw;
    }
    @include desktop {
      width: 50rem;
      font-size: 210px;
    }
  }
  &__slogan {
    font-family: "MontserratR";
    letter-spacing: 1px;
    box-sizing: border-box;
    // width: 59.0624vw;
    width: 79.0624vw;
    // color: $white;
    background: -webkit-linear-gradient(#eee, #333);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    font-size: 6.25vw;
    text-align: center;
    &-span {
      font-family: "MontserratT";
      text-shadow: 0 0px 6px white;
    }
    @include tablet {
      // width: 51.0624vw;
      width: 71.0624vw;

      font-size: 5.42vw;
    }
    @include desktop {
      width: 46.375rem;
      font-size: 4.3438rem;
    }
  }
  &__button-container {
    display: flex;
    justify-content: center;
  }
  &__button {
    font-family: "MontserratT";
    letter-spacing: 1px;
    background: linear-gradient(#eee, #333);
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 1.0625rem;
    font-weight: 600;
    color: $black;
    border-radius: 20px;
    border: none;
    outline: none;
    padding: 0.4375rem 1rem;
    width: 12.875rem;
    height: 2.25rem;
    @include tablet {
      width: 16.875rem;
      font-size: 1.375rem;
    }
    &:hover {
      background: transparent;
      color: $white;
      border: none;
      border-bottom: 3px solid $white;
      border-bottom-left-radius: 0;
      border-bottom-right-radius: 0;
      transition: 0.1s;
      cursor: pointer;
    }
    &-link {
      text-decoration: none;
    }
  }
}
